"use client";

import Navbar from "../../components/Navbar";
import Brandlabels from "@/components/ui/Brandlabels";
import { motion, useAnimation } from "framer-motion";
import { useEffect, useState } from "react";

const scarves = [
  "/images/bs-img.png",
  "/images/bs1-img.png",
  "/images/bs2-img.png",
  "/images/bs3-img.png",
];

export default function Home() {
  const [currentScarf, setCurrentScarf] = useState(0);

  // cycle scarves every 3 seconds
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentScarf((prev) => (prev + 1) % scarves.length);
    }, 3000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="min-h-screen w-full bg-[url('/images/bg_one.jpg')] bg-cover bg-center">
      <Navbar />

      <div className="text-center mt-10 relative inline-block">
        <motion.h1
          key={currentScarf} // forces re-render when image changes
          className="text-center font-bold text-black big-text -mt-10"
          style={{
            backgroundImage: `url(${scarves[currentScarf]})`,
            backgroundSize: "cover",
            backgroundRepeat: "no-repeat",
            backgroundPosition: "center",
            WebkitBackgroundClip: "text",
            WebkitTextFillColor: "transparent",
            display: "inline-block",
          }}
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1 }}
        >
          DIVINITY
        </motion.h1>
      </div>
      <p className="text-center font-bold text-black text-[15px] sm:text-[25px] -mt-20">
        EXCLUSIVE FOULARD COLLECTION
      </p>
      <Brandlabels />

      <div className="flex justify-end px-10 mt-15 mb-5">
        <button className="primary-btn">Shop Now</button>
      </div>
    </div>
  );
}
